#about {
    width: 100vw;
    max-width: 65rem;
    margin: 0 auto;
    overflow: hidden;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.aboutTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
}

.aboutContent {
    height: 100vh;
    width: 100vw;
    font-size: 1.5rem;
    font-weight: 300;

}

.aboutColor {
    color: aqua;
}

.skillsTitle {
    text-align: left;
    font-size: 2rem;
    color: rgb(150,150,150);
    padding: 1rem;
    font-weight: 800;
}

.skillsBox {
    background-color: rgb(30, 30, 30);
    margin: 1.5rem;
    padding: 1rem;
    overflow-y: auto;
    height: calc(45% - 7.25rem);
}

.ul{
    height: 500px;
}

.skillList {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.skillTag {
    background: aqua;
    width: auto;
    margin: .5rem;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1rem;
    color: rgb(30,30,30);
    font-weight: 800;
}

@media screen and (max-width:720px) {
    .aboutContent {
        padding-left: 3%;
        padding-right: 3%;
    }

    .skillsTitle{
        font-size:1.75rem;
        padding:.5rem;
    }
}
