.navbar {
    height: 7rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
    /*background-color: rgb(45,45,45);*/
    background: linear-gradient(to bottom, rgba(45, 45, 45, 1), rgba(45, 45, 45, 1) 90%, rgba(45, 45, 45, 0) 100%);
}

.logo{
    object-fit: cover;
    height: 5rem;
    width: 5rem;
}

.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
    font-size: 1.25rem;
}

.desktopMenuListItem:hover{
    color: aqua;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid aqua;
    
}

.link {
    object-fit: cover;
    height: 2rem;
    width: 2rem;
    margin: 0.5rem 0.5rem 0rem;
    position: relative;
    bottom: -7.5px;
}

.link:hover {
    filter: invert(87%) sepia(27%) saturate(2085%) hue-rotate(122deg) brightness(2500%) contrast(107%);
}

.navMenu{
    position:absolute;
    top:4rem;
    right:2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.listItem{
    color:white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30,30);
}

.mobileMenu {
    display: none;
    object-fit: cover;
    height: 1.8rem;
    cursor: pointer;
}

@media screen and (max-width:720px){
    .mobileMenu{
        display: flex;
    }

    .desktopMenu{
        display: none;
    }
}
